<template>
	<view class="function-body">
		<Tabs @returnIndex="changeFunIndex" :title="functionTitle" :functionIndex="functionIndex"></Tabs>
		
		<!-- 市集 -->
		<view v-show="functionIndex==0">
			<view class="title">
				
				<!-- 购买 or 卖出 -->
				<view class="title-option" v-for="(item,index) in marketTitle" :key="index"
				@click="changeMarIndex(index)">
					<view class="title-bar" :class="{barActive:marketIndex===index}"></view>
					<text class="title-name" :class="{textActive:marketIndex===index}">{{item}}</text>
				</view>
				
				<!-- 进度 -->
				<view class="title-sort" @tap="showSpringBox1=(showSpringBox1+1)%2">
					<text class="springText">进度</text>
					<text v-if="showSpringBox1==false" class="springBoxDown">
						<svg xmlns="http://www.w3.org/2000/svg" width="10" height="6" viewBox="0 0 10 6">
							<path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" transform="translate(10 6) rotate(180)" fill="#4F73A5"/>
						</svg>
					</text>
					<text v-else class="springBoxUp">
						<svg xmlns="http://www.w3.org/2000/svg" width="10" height="6" viewBox="0 0 10 6">
						  <path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" fill="#4f73a5"/>
						</svg>
					</text>
				</view>
				<view class="arrivalNavigation" v-show="showSpringBox1">
					<view class="sideNavigation">
						<template v-for="(item,index) in progress">
							<view v-show="index!=0" class="liBottomBorder"></view>
							<view class="navigatorStyle" @tap="" :key="index">
								<text>{{item}}</text>
							</view>
						</template>
					</view>
				</view>
				
			</view>
			
			<view class="content" v-if="">
				<view class="goods-item" v-for="(item,index1) in showMarket" :key="item.id" @tap="toClassify(item.id)">
				    <view class="item-img">
						<image class="item-pic" mode="scaleToFill" :src="item.introImage"></image>
						
						<view class="seller-item">
							<image class="seller-pic" @tap="toSeller" :src="item.userImage"></image>
							<text class="seller-name">{{item.userName}}</text>
						</view>
					</view>
				    <view class="item-block">
				        <view class="item-detail">
				            <text class="item-title">{{item.name}}</text>
				            <text class="item-date">下单时间 {{item.time}}</text>
				            <text class="item-payment">实收款 ￥{{item.totalPrice}}</text>
				        </view>
						<!-- 0:未确认，1:未发货，2:未收货，3:未评价，4:退款中，5:已评价 -->
						<view class="sell-item" @tap="ordersMgt1">
							<text v-if="item.status==0">未确认</text>
							<text v-if="item.status==1">未发货</text>
							<text v-if="item.status==2">未收货</text>
							<text v-if="item.status==3">未评价</text>
							<text v-if="item.status==4">退款中</text>
							<text v-if="item.status==5">已评价</text>
						</view>
						<view class="sell-icon" @tap="ordersMgt2">···</view>
				    </view>
				</view>
			</view>
		</view>
		
		<!-- 拍卖 -->
		<view v-show="functionIndex==1">
			<view class="title">
				<!-- 竞拍 or 拍品 -->
				<view class="title-option" v-for="(item,index) in auctionTitle" :key="index"
				@click="changeAucIndex(index)">
					<view class="title-bar" :class="{barActive:auctionIndex===index}"></view>
					<text class="title-name" :class="{textActive:auctionIndex===index}">{{item}}</text>
				</view>
				
				<!-- 进度 -->
				<view class="title-sort" @tap="showSpringBox2=(showSpringBox2+1)%2">
					<text class="springText">进度</text>
					<text v-if="showSpringBox2==false" class="springBoxDown">
						<svg xmlns="http://www.w3.org/2000/svg" width="10" height="6" viewBox="0 0 10 6">
							<path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" transform="translate(10 6) rotate(180)" fill="#4F73A5"/>
						</svg>
					</text>
					<text v-else class="springBoxUp">
						<svg xmlns="http://www.w3.org/2000/svg" width="10" height="6" viewBox="0 0 10 6">
						  <path id="多边形_10" data-name="多边形 10" d="M5,0l5,6H0Z" fill="#4f73a5"/>
						</svg>
					</text>
				</view>
				<view class="arrivalNavigation" v-show="showSpringBox2">
					<view class="sideNavigation">
						<template v-for="(item,index) in progress">
							<view v-show="index!=0" class="liBottomBorder"></view>
							<view class="navigatorStyle" @tap="" :key="index">
								<text>{{item}}</text>
							</view>
						</template>
					</view>
				</view>
				
			</view>
		
		
			<view class="content">
				<view class="goods-item" v-for="(item,index1) in currentDetail" :key="item.id" @tap="toClassify(item.id)">
				    <view class="item-img">
						<image class="item-pic" mode="scaleToFill" :src="item.introImage"></image>
						
						<view class="seller-item">
							<image class="seller-pic" @tap="toSeller"></image>
							<text class="seller-name">name</text>
						</view>
					</view>
				    <view class="item-block">
				        <view class="item-detail">
				            <text class="item-title">name</text>
				            <text class="item-date">拍得时间</text>
				            <text class="item-payment">最终竞拍价格 ￥</text>
				        </view>
						<view class="sell-item" @tap="ordersMgt1">
							<text>去发货</text>
						</view>
						<view class="sell-icon" @tap="ordersMgt2">···</view>
				    </view>
				</view>
			</view>
			
		</view>
		
		
			
	</view>
</template>

<script>
	export default {
		data() {
			return {
				functionTitle:["市集","拍卖"],
				functionIndex:0,
				progress:["未确认","未发货","未收货","未评价","退款中","已评价"],
				currentDetail:["",""],
				showSpringBox1:false,
				showSpringBox2:false,
				
				//市集——购买 0:未确认，1:未发货，2:未收货，3:未评价，4:退款中，5:已评价
				marketTitle:["我的购买","我的卖出"],
				marketIndex:0,
				page1_1:0,
				marketByuer:[],
				marketByuer0:[],
				marketByuer1:[],
				marketByuer2:[],
				marketByuer3:[],
				marketByuer4:[],
				marketByuer5:[],
				
				//市集——卖出
				page1_2:0,
				marketSeller:[],
				marketSeller0:[],
				marketSeller1:[],
				marketSeller2:[],
				marketSeller3:[],
				marketSeller4:[],
				marketSeller5:[],
				
				showMarket:[],
				
				//拍卖——购买
				auctionTitle:["我的竞拍","我的拍品"],
				auctionIndex:0,
				page2_1:0,
				auctionByuer:[],
				auctionByuer0:[],
				auctionByuer1:[],
				auctionByuer2:[],
				auctionByuer3:[],
				auctionByuer4:[],
				auctionByuer5:[],
				
				//拍卖——卖出
				page2_2:0,
				auctionSeller:[],
				auctionSeller0:[],
				auctionSeller1:[],
				auctionSeller2:[],
				auctionSeller3:[],
				auctionSeller4:[],
				auctionSeller5:[],
				
				showAuction:[],
				
				count:3,
			}
		},
		methods: {
			//切换tab选项卡的标题
			changeFunIndex(index){
				this.functionIndex=index
				console.log(index)
			},
			changeMarIndex(index){
				this.marketIndex=index
				console.log(index)
				if(this.marketIndex==0) this.showMarket=this.marketByuer
				else this.showMarket=this.marketSeller
			},
			changeAucIndex(index){
				this.auctionIndex=index
				console.log(index)
				if(this.auctionIndex==0) this.showAuction=this.auctionByuer
				else this.showAuction=this.auctionSeller
			},
			ordersMgt1(){
				console.log("Mgt1")
			},
			ordersMgt2(){
				wx.showActionSheet({
				     itemList: ["查看物流", "取消订单"],
				     success: function (res) {
				       if (!res.cancel) {
				         console.log(res.tapIndex)//这里是点击了那个按钮的下标
				       }
				     }
				})
			},
			
			async getMarketsBuy(k){
				console.log(k)
				let res = await this.$myRequest({
					url: "/order/buyer",
					data:{
						id:2, //test
						type:0, //0:普通商品，1:拍卖品
						status:k, //0:未确认，1:未发货，2:未收货，3:未评价，4:退款中，5:已评价
						pageNum:this.page1_1,
						pageSize:this.count,
					},
				})
				for(let i=0;i<res.data.data.length;i++){
					res.data.data[i].time=this.$formatDate(res.data.data[i].time)
					let image = await this.$myRequest({
						url: "/user/baseInformation",
						data:{
							id:res.data.data[i].buyerId,
						},
					})
					res.data.data[i]['userImage']=image.data.data.portrait
					res.data.data[i]['userName']=image.data.data.name
				}
				if(k==0) this.marketByuer0=[...this.marketByuer0,...res.data.data]
				else if(k==1) this.marketByuer1=[...this.marketByuer1,...res.data.data]
				else if(k==2) this.marketByuer2=[...this.marketByuer2,...res.data.data]
				else if(k==3) this.marketByuer3=[...this.marketByuer3,...res.data.data]
				else if(k==4) this.marketByuer4=[...this.marketByuer4,...res.data.data]
				else if(k==5) this.marketByuer5=[...this.marketByuer5,...res.data.data]
				
				return res;
			},
			async marketsBuy(){
				for(let k=0;k<=5;k++){
					await this.getMarketsBuy(k)
					console.log(k)
				}
				this.page1++;
				this.marketByuer=[...this.marketByuer,...this.marketByuer0]
				this.marketByuer=[...this.marketByuer,...this.marketByuer1]
				this.marketByuer=[...this.marketByuer,...this.marketByuer2]
				this.marketByuer=[...this.marketByuer,...this.marketByuer3]
				this.marketByuer=[...this.marketByuer,...this.marketByuer4]
				this.marketByuer=[...this.marketByuer,...this.marketByuer5]
				
				if(this.marketIndex==0) this.showMarket=this.marketByuer
				else this.showMarket=this.marketSeller
			},
			
			
			async getAuctionsBuy(k){
				let res = await this.$myRequest({
					url: "/order/seller",
					data:{
						id:1, //test
						type:0, //0:普通商品，1:拍卖品
						status:k, //0:未确认，1:未发货，2:未收货，3:未评价，4:退款中，5:已评价
						pageNum:this.page1_2,
						pageSize:this.count,
					},
				})
				for(let i=0;i<res.data.data.length;i++){
					res.data.data[i].time=this.$formatDate(res.data.data[i].time)
					let image = await this.$myRequest({
						url: "/user/baseInformation",
						data:{
							id:res.data.data[i].buyerId,
						},
					})
					res.data.data[i]['userImage']=image.data.data.portrait
					res.data.data[i]['userName']=image.data.data.name
				}
				if(k==0) this.marketByuer0=[...this.marketByuer0,...res.data.data]
				else if(k==1) this.marketByuer1=[...this.marketByuer1,...res.data.data]
				else if(k==2) this.marketByuer2=[...this.marketByuer2,...res.data.data]
				else if(k==3) this.marketByuer3=[...this.marketByuer3,...res.data.data]
				else if(k==4) this.marketByuer4=[...this.marketByuer4,...res.data.data]
				else if(k==5) this.marketByuer5=[...this.marketByuer5,...res.data.data]
				
				return res;
			},
			async auctionsBuy(){
				for(let k=0;k<=5;k++){
					await this.getMarketsBuy(k)
				}
				this.page1++;
				this.marketByuer=[...this.marketByuer,...this.marketByuer0]
				this.marketByuer=[...this.marketByuer,...this.marketByuer1]
				this.marketByuer=[...this.marketByuer,...this.marketByuer2]
				this.marketByuer=[...this.marketByuer,...this.marketByuer3]
				this.marketByuer=[...this.marketByuer,...this.marketByuer4]
				this.marketByuer=[...this.marketByuer,...this.marketByuer5]
				
				if(this.marketIndex==0) this.showMarket=this.marketByuer
				else this.showMarket=this.marketSeller
			}
		},
		
		/**
		 * 生命周期函数--监听页面加载
		 */
		onLoad: function(option) {
			if(option.type){
				this.functionIndex = Number(option.type)
			}
			this.marketsBuy();
		},
		/**
		 * 生命周期函数--监听页面初次渲染完成
		 */
		onReady: function() {},
		/**
		 * 生命周期函数--监听页面显示
		 */
		onShow: function() {},
		/**
		 * 生命周期函数--监听页面隐藏
		 */
		onHide: function() {},
		/**
		 * 生命周期函数--监听页面卸载
		 */
		onUnload: function() {
		},
		/**
		 * 页面相关事件处理函数--监听用户下拉动作
		 */
		onPullDownRefresh: function() {},
		/**
		 * 页面上拉触底事件的处理函数
		 */
		onReachBottom: function() {},
		/**
		 * 用户点击右上角分享
		 */
		onShareAppMessage: function() {},
	}
	
</script>

<style>
	@import url("./myorders.css");
</style>
